<template>
    <div id="nav">
        <div class="nav-logo" @click="goTo('header')">
            <h2>JONES<span>C</span>ODE</h2>
            <div class="tiny-logo">
                <div id="no1" class="redsquare"></div>
                <div id="no2" class="redsquare"></div>
                <div id="no3" class="redsquare"></div>
            </div>
        </div>
        <ul class="nav-menu">
            <li><button @click="goTo('about')"><a class="nav-link" >About</a></button></li>
            <li><button @click="goTo('projects')"><a class="nav-link" >Projects</a></button></li>
            <li><button @click="goTo('contact')"><a class="nav-link" >Contact</a></button></li>
        </ul>
    </div>
  
</template>

<script>
export default {
    name: 'NavBar',
    created () {
    window.addEventListener('scroll', this.handleScroll);
    },
    unmounted () {
    window.removeEventListener('scroll', this.handleScroll);
    },
    methods: {
        goTo(thisId) {
            console.log(document.querySelector('#'+thisId))
            let el = document.querySelector('#'+thisId);
            let top = el.offsetTop;

            window.scrollTo(0, top-90);
        },
        handleScroll() {
            if (document.body.scrollTop > 250 || document.documentElement.scrollTop > 250) {
            document.getElementById("nav").classList.add("nav-scrolled");
            } else {
        document.getElementById("nav").classList.remove("nav-scrolled");
            }
        }
        
  }

}
    
</script>

<style scoped>
.nav-scrolled{
    background-color: rgb(255, 0, 0);
    color: white;
}
.nav-scrolled h2{
    color: rgb(0, 0, 0);
}
.nav-scrolled .nav-logo .tiny-logo div{
    background-color: white;
}
h2 {
    color: rgb(255, 50, 0);
}


.redsquare {
    height: 4px;
    width: 4px;
    background-color: red;
}
span {
    color: white;
}
#nav {
    position: fixed;
    width: 100vw;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    top: 0;
    padding: 0px 1rem;
    z-index: 1;
}
.nav-logo {
    position: relative;
    align-self: center;
    display: flex;
    flex-direction: row;
}
#no1 { 
    position: absolute;
    right: 4px;
    bottom:14px;
}
#no2 { 
    position: absolute;
    bottom:14px;
}
#no3 { 
    position: absolute;
    right: 0px;
    bottom:10px;
}
.nav-menu {
    display: flex;
    flex-direction: row;
    margin-right: 2rem;
    justify-content: space-evenly;
}
li {
    list-style: none;
}
.nav-link {
    text-decoration: none;
}

@media only screen and (min-width: 700px) {
    h2 {
        font-size: 1.8rem;
            margin: 1rem;
    }
    .nav-link {
        padding: 10px;
    }
   
}

</style>